.defineRoom {
  padding-top: 2%;
  margin-bottom: 80px;

  .deleteIconOutline {
    border: 1.5px solid #d2d3d4;
    color: #bf3939;
    width: 32px;
    height: 32px;
    border-radius: 8px;
    font-size: 16px;
    padding: 7px;
    float: center;
    margin-top: 38px;
    font-weight: 600;
  }

  .bsPlusCircleOutline {
    border: 1.5px solid #d2d3d4;
    color: #935ed6 !important;
    width: 30px;
    height: 30px;
    border-radius: 8px;
    font-size: 14px;
    padding: 4px;
    float: center;
    margin-top: 38px;
    font-weight: 700;
    &:hover {
      color: #391765 !important;
    }
  }

  .bsPlusCircleColor {
    color: #935ed6 !important;
    font-size: 14px;
    font-weight: 600;
    line-height: 20px;
    letter-spacing: 0em;
    text-align: left;
    display: flex;
    align-items: center;

    &:hover {
      color: #391765 !important;
    }
  }

  .deleteRoomType {
    font-size: 13px;
    font-weight: 600;
    line-height: 20px;
    color: #bf3939;
    display: flex;
    align-items: center;
    margin-top: 4px;
  }

  .verticalLine {
    display: flex;
    align-items: center;
  }
  .tableWrap {
    border: 1px solid#E8E9E9;
    border-radius: 9px;
    overflow: hidden;
    margin-top: 16px;
    width: max-content;
  }
  .table {
    width: 722px;
    
    thead {
      tr {
        th {
          background-color: #E2F8FB;
          color:#2A5067;
          font-family: Inter;
          font-weight: 500;
          font-size: 14px;
          line-height: 20px;
          padding: 14px 16px;
        }
      }
    }
    tbody {
      tr {
        td {
          padding: 8px 16px;
          vertical-align: bottom;
          border: 0 !important;
        }
      }
    }
  }
}

.backgroundEvenn{
  background: #fff !important;

}
.backgroundOdd{
  background: #f7f7f7!important;
}